<template>
  <div style="">
    <div style="margin-top:10px;width:700px;padding:20px;">
      setJsonDatachildren
      <br>
      treelink
    </div>
    <div v-loading="g_loading" style="width: calc(100% - 10px);height:calc(100vh - 140px);border-top: #efefef solid 1px;">
      <SeeksRelationGraph ref="seeksRelationGraph" :options="graphOptions">
      </SeeksRelationGraph>
    </div>
    <el-button type="success" class="c-show-code-button">
      <el-link href="https://github.com/seeksdream/relation-graph/blob/master/doc/demo/Demo4TreeData.vue" target="_blank" style="color: #ffffff;">
      </el-link>
    </el-button>
  </div>
</template>

<script>
import SeeksRelationGraph from 'relation-graph'
  export default {
    name: 'SeeksRelationGraphDemo',
    components: {SeeksRelationGraph},
    data() {
      return {
        g_loading: true,
        demoname: '---',
        graphOptions: {
          debug: true,
          'backgrounImage': 'http://ai-mark.cn/images/ai-mark-desc.png',
          'backgrounImageNoRepeat': true,
          'layouts': [
            {
              'label': '',
              'layoutName': 'tree',
              'layoutClassName': 'seeks-layout-center',
              'defaultJunctionPoint': 'border',
              'defaultNodeShape': 0,
              'defaultLineShape': 1,
              'from': 'left',
              // 4 tree-&
              'min_per_width': '200',
              'max_per_width': '500',
              'min_per_height': '40',
              'max_per_height': '60',
              'levelDistance': '' // 4
            }
          ],
          "defaultExpandHolderPosition": "right",
          'defaultNodeShape': 1,
          'defaultNodeWidth': '100',
          'defaultLineShape': 4,
          'defaultJunctionPoint': 'lr',
          'defaultNodeBorderWidth': 0,
          'defaultLineColor': 'rgba(0, 186, 189, 1)',
          'defaultNodeColor': 'rgba(0, 206, 209, 1)'
        }
      }
    },
    created() {
    },
    mounted() {
      this.demoname = this.$route.params.demoname
      this.setGraphData()
    },
    methods: {
      setGraphData() {
        // expandHolderPosition: 'right'  expanded: false ""
        //         onNodeExpand
        var __graph_json_data = {
          'rootId': 'a',
          'nodes': [
            {'id': 'a', 'text': 'a', 'children': [
                {'id': 'b', 'text': 'b', 'children': [
                    {'id': 'b1', 'text': 'b1', 'children': [
                        {'id': 'b1-1','text': 'b1-1'},
                        {'id': 'b1-2', 'text': 'b1-2'},
                        {'id': 'b1-3', 'text': 'b1-3'},
                        {'id': 'b1-4','text': 'b1-4'},
                        {'id': 'b1-5', 'text': 'b1-5'},
                        {'id': 'b1-6', 'text': 'b1-6'},
                      ]
                    },
                    {'id': 'b2', 'text': 'b2', 'children': [
                        {'id': 'b2-1','text': 'b2-1'},
                        {'id': 'b2-2', 'text': 'b2-2'},
                      ]
                    },
                  ]
                },
                {'id': 'c', 'text': 'c', 'children': [
                    {'id': 'c1', 'text': 'c1' },
                    {'id': 'c2', 'text': 'c2' },
                    {'id': 'c3','text': 'c3' }
                  ]
                },
              ]
            },
            {
              'id': 'e', 'text': 'e', 'children': [
                {
                  'id': 'e1', 'text': 'e1'
                },
                {
                  'id': 'e2', 'text': 'e2'
                }
              ]
            }
          ],
          'links': [
            // 
          ]
        }
        console.log(JSON.stringify(__graph_json_data))
        this.g_loading = false
        this.$refs.seeksRelationGraph.setJsonData(__graph_json_data, (seeksRGGraph) => {
          // 
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


